<!--  -->
<template>
<div>
    <el-card>
        <h1>Thank you!</h1>
        <div class="context"><span>We are pleased to confirm your booking for car detailing service.</span><br></div>
        <div class="context">
             <div><span>Date:</span>{{date}}</div>
        <div><span>Time:</span>{{showPeriod(time)}}</div>
        <div><span>Service(s):</span></div>
        <div v-show="checkboxGroup1.length>0">&nbsp;&nbsp;Sedan & Hatchback:
                
                    <span v-for="c in checkboxGroup1"  :key="c">{{c}}</span>&nbsp;
                           
            </div>
            <div v-show="checkboxGroup2.length>0">&nbsp;&nbsp;SUV & UTE:
                
                <span v-for="c in checkboxGroup2"  :key="c">{{c}}</span>&nbsp;
                      
            </div>
            <div v-show="checkboxGroup3.length>0">&nbsp;&nbsp;7-SEATER, VAN & LIGHT TRUCK:
                
                <span v-for="c in checkboxGroup3"  :key="c">{{c}}</span>&nbsp;
                          
            </div>
        </div>
       
        <div class="context"><span>If you have any special requests or specific instructions for the service, please let us know in advance. We want to ensure that your experience with us is tailored to your preferences.</span>
        </div>
        <div class="context"><span>
            If you need to make any changes to your booking or if you have any questions, please feel free to reach out to us. Our contact information can be found on the homepage.
        </span></div>
        <div class="context"><span>Thank you for choosing Hao's Garage. We look forward to providing you with exceptional service. </span></div>
    </el-card>
    <el-button @click="back" type="primary" class="button"> Get back to Home Page</el-button>
</div>
</template>

<script>
export default {
    name:"receipt-page",
data() {
return {
    date:'',
    time:'',
    checkboxGroup1:[],
    checkboxGroup2:[],
    checkboxGroup3:[],
    form:{},

}
},
//生命周期 - 创建完成（访问当前this实例）
created() {
    this.date=JSON.parse(localStorage.getItem('chosenDate'))
    this.time=JSON.parse(localStorage.getItem('chosenTime'))
    this.checkboxGroup1=JSON.parse(localStorage.getItem('checkboxGroup1'))
    this.checkboxGroup2=JSON.parse(localStorage.getItem('checkboxGroup2'))
    this.checkboxGroup3=JSON.parse(localStorage.getItem('checkboxGroup3'))
},
//生命周期 - 挂载完成（访问DOM元素）
mounted() {


},
methods:{
    back(){
        this.$router.push("/index")
    },
    showPeriod(period){
            var label=""
            switch(period){
                    case 0:
                        label="7:30"
                        break
                    case 1:
                        label="8:30"
                        break
                    case 2:
                        label="9:30"
                        break
                    case 3:
                        label="10:30"
                        break
                    case 4:
                        label="11:30"
                        break
                    case 5:
                        label="12:30"
                        break
                    case 6:
                        label="13:30"
                        break
                    case 7:
                        label="14:30"
                        break
                    case 8:
                        label="15:30"
                        break
                    case 9:
                        label="16:30"
                        break
                    case 10:
                        label="17:30"
                        break
                }
                return label
        },

}
}
</script>
<style scoped>
/* @import url(); 引入css类 */
.el-card{
    width: 50%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    text-align: left;
    word-break:keep-all;
    font-size:25px;
}

.context{
    margin-top: 30px;
}

.button{
    margin-top: 30px;
    font-size:25px

}

@media screen and (max-width: 400px)  { 
    .el-card{
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    text-align: left;
    word-break:keep-all;
    font-size:16px;
}

.context{
    margin-top: 20px;
}

.button{
    margin-top: 30px;
    font-size:16px

}
   }
</style>