import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../components/Home.vue"
import Step1 from "../components/Booking/Step1.vue"
import Step2 from "../components/Booking/Step2.vue"
import Step3 from "../components/Booking/Step3.vue"
import AdminHome from "../components/Admin/AdminHome.vue"
import ManageBookings from "../components/Admin/ManageBookings.vue"
import Login from "../components/Admin/Login.vue"
import SetTimeUnavailable from "../components/Admin/SetTimeUnavailable.vue"
import Receipt from "../components/Booking/Receipt.vue"


Vue.use(VueRouter);

const router=new VueRouter({
    // mode:"history",
    base: process.env.BASE_URL,
    history:false,
    routes:[
        {
            path:"/",
            redirect:"/index",
        },
        {
            path:"/index",
            component: Home,
        },
        {
            path:"/booking1",
            component: Step1
        },
        {
            path:"/booking2",
            component: Step2
        },
        {
            path:"/booking3",
            component: Step3
        },
        {
            path:"/adminLogin",
            component:Login

        },
       
        {
            path:"/admin",
            component:AdminHome,
            redirect:"/admin/manageBookings",
            children:[
                {               
                    path:"/admin/manageBookings",
                    component: ManageBookings
                    
                },
                {
                    path:"/admin/setTimeUnavailable",
                    component: SetTimeUnavailable
                }
            ]
        },
        {
            path:"/receipt",
            component:Receipt
        }


    ]
}
);

router.beforeEach((to,from,next)=>{
    if(to.path==="/admin" ||to.path==="/admin/manageBookings" ){
      
        if(localStorage.getItem('ifLogin')=='true'){
            next()
        }else{
            alert("You haven't logged in")
            router.push('/adminLogin')   
        }
    }else{
      next()
    }

})

export default router;
    
