<!--  -->
<template>
<div>
    <el-date-picker
            v-model="bookingTime"
            type="date"
            placeholder="Choose the date"
            :picker-options="pickerOptions">
            </el-date-picker>
    
    <div v-show="ifThisDayAvailable">
        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
        <el-checkbox-group v-model="chosenTime">
            <el-checkbox   v-for="a in availableTime" :label="a" :key="a" style="display:block"  border>{{showPeriod(a)}} </el-checkbox>
        </el-checkbox-group>
    </div>
    
        <div >
             <el-button @click="submit" type="primary" width="20vw" font-size="2vw" >Set Unavailable</el-button>
        </div>
       
</div>
</template>

<script>
var moment = require('moment');
export default {
name:"set-time-unavailable",
data() {
return {
    bookingTime:new Date(new Date().toLocaleDateString()).getTime()+24*60*60*1000,
    pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          }
        },
    
    
    ifThisDayAvailable:false,
    availableTime:[],
    chosenTime:[],
    checkAll: false,
    isIndeterminate: true

}
},

methods:{

    handleCheckAllChange(val) {
        this.chosenTime = val ? this.availableTime : [];
        this.isIndeterminate = false;
      },
      
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
    
      disabledDate(date){
            return date <new Date();
    },

    getAvailableTime(){
            this.$http.post('/getTimeByAdmin',
            {               
                date:moment(this.bookingTime).format("YYYY-MM-DD"),
            }
            )
            .then((res) => {
                if (res.data.flag) {
                  this.$message.success(res.data.message);
                  this.ifThisDayAvailable=res.data.available
                  this.availableTime=res.data.availableTime
                } else {
                  this.$message.error(res.data.message);
                }
            });
        },

    submit(){
        this.$http.post('/setTimeUnavailable',
        {chosenTime:this.chosenTime,
        date:moment(this.bookingTime).format("YYYY-MM-DD")})
        .then((res)=>{
            if(res.data.flag){
                this.$message.success(res.data.message)
                this.getAvailableTime()
            }else{
                this.$message.error(res.data.message)
            }
            
        })
    },

    showPeriod(period){
            var label=""
            switch(period){
                    case 0:
                        label="7:30"
                        break
                    case 1:
                        label="8:30"
                        break
                    case 2:
                        label="9:30"
                        break
                    case 3:
                        label="10:30"
                        break
                    case 4:
                        label="11:30"
                        break
                    case 5:
                        label="12:30"
                        break
                    case 6:
                        label="13:30"
                        break
                    case 7:
                        label="14:30"
                        break
                    case 8:
                        label="15:30"
                        break
                    case 9:
                        label="16:30"
                        break
                    case 10:
                        label="17:30"
                        break
                    case 11:
                        label="18:30"
                        break
                }
                return label
        },
},
//生命周期 - 创建完成（访问当前this实例）
created() {

},
//生命周期 - 挂载完成（访问DOM元素）
mounted() {
    this.getAvailableTime()
    if(this.availableTime.length!=0){
        this.ifThisDayAvailable=true
    }

},

watch:{
    bookingTime:function () {
           
           // console.log("time changed",this.bookingTime)
           this.getAvailableTime()

       },
    //    chosenTime:function(){
    //     console.log(this.chosenTime)
    //    }

}
}
</script>
